.selected_container {
    display: flex;
    align-items: flex-start;
    gap: 20px;
}

.selected_container img {
    width: 400px;
    height: auto;
}

.container_info {
    flex: 1;
}

.container_info h1 {
    font-size: 24px;
    margin: 0;
    margin-bottom: 10px;
}

.container_info h4 {
    margin-top: 5px;
    margin-bottom: 20px;
    font-size: 16px;
    color: #393636;
}

.container_info h2 {
    font-size: 20px;
    margin-bottom: 5px;
}

.container_info p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;
    max-width: 450px;
    text-align: justify;
}

.container_info h3 {
    font-size: 18px;
    margin-bottom: 5px;
}

.container_info span {
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 5px;
    padding: 5px 10px;
    background-color: #f0f0f0;
    border-radius: 3px;
    font-size: 14px;
}